      <div class="video-wrapper">
        <video ref="video2" id="my-video-2" class="video-play video-js aos-init aos-animate" data-aos="fade-in" controls muted playsinline loop preload="auto">
          <source src="@/assets/sub-video/video11.mp4" type="video/mp4" />
        </video>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="row d-flex justify-content-center">
          <button type="button" class="next-btn btn btn-outline-primary btn-lg" @click="goToTargetPage(15)">
            於日治時期建成、建造時混合了咕咾石、並採用與總督府相同的「TR磚」、現為杜氏家族所有、忘憂居是一棟二層樓的紅磚老厝。
          </button>
        </div>
        <div class="row d-flex justify-content-center">
          <button type="button" class="next-btn btn btn-outline-primary btn-lg" @click="goToTargetPage(15)">
            並採用與總督府相同的「TR磚」、於日治時期建成、建造時混合了咕咾石、忘憂居是一棟二層樓的紅磚老厝、現為杜氏家族所有。
          </button>
        </div>
        <div class="row d-flex justify-content-center">
          <button type="button" class="next-btn btn btn-outline-primary btn-lg" @click="goToTargetPage(15)">
            忘憂居是一棟二層樓的紅磚老厝、現為杜氏家族所有、並採用與總督府相同的「TR磚」、建造時混合了咕咾石、於日治時期建成。
