<script>
export default {
  name: 'NavbarComponent',
  props: {
    activePage: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      activePageNum: 0,
      lastScrollTop: 0,
      debounceTimeout: null,
      debounceDelay: 50, // 防抖延迟
    };
  },
  mounted() {
    this.setupIntersectionObserver();
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    goToTargetPage(num) {
      this.$emit('updatePageIndex', num);
      this.activePageNum = num;
      this.$router.push({ name: 'SubPage' + num });
    },
    setupIntersectionObserver() {
      const options = {
        root: null,
        rootMargin: '0px',
        threshold: 0.5
      };

      const callback = (entries) => {
        entries.forEach(entry => {
          const video = entry.target;
          if (entry.isIntersecting) {
            video.play();
          } else {
            video.pause();
          }
        });
      };

      const observer = new IntersectionObserver(callback, options);
      this.$refs.video1 && observer.observe(this.$refs.video1);
      this.$refs.video2 && observer.observe(this.$refs.video2);
    },
    handleScroll() {
      if (this.debounceTimeout) {
        clearTimeout(this.debounceTimeout);
      }

      this.debounceTimeout = setTimeout(() => {
        const scrollTop = window.scrollY || document.documentElement.scrollTop;
        if (scrollTop > this.lastScrollTop) {
          this.positionVideo('down');
        } else if (scrollTop < this.lastScrollTop) {
          this.positionVideo('up');
        }
        this.lastScrollTop = scrollTop <= 0 ? 0 : scrollTop;
      }, this.debounceDelay);
    },
    positionVideo(direction) {
      const videos = [this.$refs.video1, this.$refs.video2];
      const viewportHeight = window.innerHeight;
      
      let visibleVideo = null;
      for (let video of videos) {
        if (video) {
          const videoRect = video.getBoundingClientRect();
          if (videoRect.top <= viewportHeight && videoRect.bottom >= 0) {
            visibleVideo = video;
            break;
          }
        }
      }

      if (visibleVideo) {
        let targetVideo = null;
        if (direction === 'down') {
          targetVideo = (visibleVideo === this.$refs.video1) ? this.$refs.video2 : null;
        } else if (direction === 'up') {
          targetVideo = (visibleVideo === this.$refs.video2) ? this.$refs.video1 : null;
        }

        if (targetVideo) {
          const targetVideoRect = targetVideo.getBoundingClientRect();
          const targetScrollToPosition = window.scrollY + targetVideoRect.top - (viewportHeight / 2) + (targetVideoRect.height / 2);

          
          const maxScrollDistance = 1000;
          const scrollDistance = Math.abs(targetScrollToPosition - window.scrollY);
          const limitedScrollDistance = Math.min(scrollDistance, maxScrollDistance);

          window.scrollTo({
            top: window.scrollY + (targetScrollToPosition > window.scrollY ? limitedScrollDistance : -limitedScrollDistance),
            behavior: 'smooth'
          });
        }
      }
    }
  }
}
</script>



